import React from "react";
import gitimg from "../pages/Images/github-image.png";
import "./Github.css";

export default function Github() {
  return (
    <div className="github-body">
      <div className="github-body-title">
        <h1 className="github">My Github</h1>
      </div>
      <a href="https://github.com/getsuga-tenshou" target="_blank">
        <img className="gitimg" src={gitimg} alt="Github" />
      </a>
    </div>
  );
}
