import React from "react";
import "./Projects.css";
import projectimg from "../pages/Images/project.png";

export default function Projects() {
  return (
    <div className="project-body">
      <div className="project-body-title">
        <h1 className="project">A list of projects i have done</h1>
      </div>
      <a href="https://www.twitter.com/in/chandan-saini-6a2138147" target="_blank">
        <img className="project-img" src={projectimg} alt="Projects" />
      </a>
    </div>
  );
}
