import React from 'react';
import './Facebook.css';
import fbimg from '../pages/Images/facebook.png'


export default function Facebook() {
  return (
    <div className="fb-body">
      <div className="fb-body-title">
        <h1 className="fb">Connect with me on Facebook</h1>
      </div>
      <a href="https://facebook.com/chandan.saini.07" target="_blank">
        <img className="fbimg" src={fbimg} alt="Facebook" />
      </a>
    </div>
  );
}
