import React from 'react';
import './Twitter.css';
import twitterimg from '../pages/Images/twitter.png';


export default function Twitter() {
  return (
    <div className="twitter-body">
      <div className="twitter-body-title">
        <h1 className="twitter">Connect with me on twitter</h1>
      </div>
      <a href="https://www.twitter.com/in/chandan-saini-6a2138147" target="_blank">
        <img className="twitter-img" src={twitterimg} alt="Twitter" />
      </a>
    </div>
  );
}
