import React from 'react';
import './LinkedIn.css';
import linkedinimg from '../pages/Images/linkedin.png';


export default function LinkedIn() {
  return (
    <div className="linkedin-body">
      <div className="linkedin-body-title">
        <h1 className="linkedin">Connect with me on LinkedIn</h1>
      </div>
      <a href="https://www.linkedin.com/in/chandan-saini-6a2138147" target="_blank">
        <img className="linkedin-img" src={linkedinimg} alt="LinkedIn" />
      </a>
    </div>
  );
}
